.card-container {
  display: flex;
  flex-direction: column;
  border: 1px solid grey;
  border-radius: 5px;
  padding: 1rem;
  background-color: #95dada;
  margin-bottom: 1rem;
  transition: width 1s;
  cursor: pointer;
  transform: translateZ(0);
  transition: transform 0.25s ease-in-out;
}
.card-container:hover {
  transform: scale(1.05);
}
